.cont__service {
    display: flex;
    flex-direction: column;
    text-align: center;
    width: 100%;
    background-color: #333;
}

.col__text {
    padding-top: 20px;
    color: #03e9f4;
}

.container__service {
    width: 100%;
    position: relative;
    display: flex;
    justify-content: space-between;
    background: #333;
}

@media (max-width: 1000px) {
    .container__service {
        width: 100%;
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        background: #333;
        align-items: center;
    }
}

.container__service .card {
    position: real;
    cursor: pointer;
    background: #333;
    padding: 1%;
}

.container__service .card .face {
    width: 300px;
    height: 200px;
    transition: 0.5s;
}

.container__service .card .face.face1 {
    position: relative;
    background: #333;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
    transform: translateY(100px);
}

.container__service .card:hover .face.face1 {
    background: #03e9f4;
    transform: translateY(0);
}

.container__service .card .face.face1 .content {
    opacity: 0.2;
    transition: 0.5s;
}

.container__service .card:hover .face.face1 .content {
    opacity: 1;
}

.container__service .card .face.face1 .content i {
    max-width: 100px;
    color: white;
}

.container__service .card .face.face1 .content h3 {
    margin: 10px 0 0;
    padding: 0;
    color: #03e9f4;
    text-align: center;
    font-size: 1.5em;
}

.container__service .card .face.face2 {
    position: relative;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    box-sizing: border-box;
    box-shadow: 0 20px 50px rgba(0, 0, 0, 0.8);
    transform: translateY(-100px);
}

.container__service .card:hover .face.face2 {
    transform: translateY(0);
}

.container__service .card .face.face2 .content p {
    margin: 0;
    padding: 0;
}

.container__service .card .face.face2 .content a {
    margin: 15px 0 0;
    display: inline-block;
    text-decoration: none;
    font-weight: 400;
    color: #333;
    padding: 5px;
    border: 1px solid #333;
}

.container__service .card .face.face2 .content a:hover {
    background: #333;
    color: #fff;
}

.content {
    text-align: center;
}

.content,
i,
h3,
p {
    background: none;
}